.about {
    background: url('https://ik.imagekit.io/hfut635vd/images/grayBg.svg?ik-sdk-version=javascript-1.4.3&updatedAt=1675833173329');
    mix-blend-mode: darken;
    padding: 3vmax 15vmax;
    text-align: center;
}

.camera_roll {
    background: url('https://ik.imagekit.io/hfut635vd/images/grayBg.svg?ik-sdk-version=javascript-1.4.3&updatedAt=1675833173329');
    mix-blend-mode: darken;
    padding: 1vmax 0 3vmax 0;
}

.camera_roll_images {
    display: flex;
    overflow-x: scroll;
}

.camera_roll_images>img {
    width: 20vmax;
}

.about>p {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 1.25vmax;
    color: #685648;
    padding: 1vmax 0;
}

.about>a {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 1.25vmax;
    text-decoration-line: underline;
    color: #F0B18E;
}